<template>
  <div class="featuresUser">
    <div class="header">
      <div class="selectNav">
        <el-select
          size="small"
          class="input_td"
          v-model="selectParams.dsjVal"
          clearable
          placeholder="请选择地市局"
          @change="dsjChange"
        >
          <el-option
            v-for="item in selectParams.dsjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          v-model="selectParams.xqjVal"
          clearable
          placeholder="请选择县区局"
          class="input_td"
          size="small"
          @change="qxjChange"
        >
          <el-option
            v-for="item in selectParams.xqjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          size="small"
          class="input_td"
          v-model="selectParams.gdsVal"
          clearable
          placeholder="请选择供电所"
        >
          <el-option
            v-for="item in selectParams.gdsOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-input
          class="input_300"
          placeholder="请输入用户编号"
          size="small"
          v-model="yhCodeInput"
          clearable
        ></el-input>
        <el-button type="primary" size="small" icon="el-icon-search" @click="GetTdmgCustList">查询</el-button>
      </div>
      <div class="selectBut">
        <el-button type="primary" size="small" @click="changeComponents(1)">停电敏感算法识别客户清单明细</el-button>
        <el-button type="primary" size="small" @click="changeComponents(2)">客户群体特征分析</el-button>
        <el-button type="primary" size="small" @click="changeComponents(3)">手工维护客户清单明细</el-button>
        <el-button type="primary" size="small" @click="changeComponents(4)">停电敏感客户手工维护</el-button>
      </div>
    </div>
    <div class="content">
      <div class="title">停电敏感客户明细列表</div>
      <div class="tableTd">
        <el-table
          v-loading="loading"
          border
          :data="tableData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
          <el-table-column prop="yhbh" label="用户编号"></el-table-column>
          <el-table-column prop="yhmc" label="用户名称"></el-table-column>
          <el-table-column prop="yddz" label="用电地址"></el-table-column>
          <el-table-column prop="dsj" label="地市局"></el-table-column>
          <el-table-column prop="xqj" label="县区局"></el-table-column>
          <el-table-column prop="gds" label="供电所"></el-table-column>
          <el-table-column prop="updateTime" label="标签更新时间"></el-table-column>
          <el-table-column prop="way" label="标记方式"></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pages.pageNo"
            :page-sizes="[8, 10, 20, 50]"
            :page-size="pages.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pages.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <el-dialog
      v-if="dialogVisible"
      :title="dialogTitle"
      fullscreen
      :visible.sync="dialogVisible"
      width="90%"
      :before-close="handleClose"
    >
      <div class="dialogCon">
        <component :is="componentName"></component>
      </div>
      <span slot="footer" class="dialog-footer"></span>
    </el-dialog>
  </div>
</template>

<script>
import distributionCharts from "./featuresUser/distributionCharts";
import outPowerListDetailed from "./featuresUser/outPowerListDetailed";
import manualManage from "./featuresUser/manualManage";
import manualListDetails from "./featuresUser/manualListDetails";
import Interface from '@/base/mixin/common-api';
import Interface2 from '@/base/mixin/power-sensitive-users';
export default {
  name: "featuresUser",
  mixins: [Interface, Interface2],
  components: {
    distributionCharts,
    outPowerListDetailed,
    manualManage,
    manualListDetails,
  },
  props: {

  },
  data() {
    return {
      //导航模块变量
      selectParams: {
        dsjOptions: [],
        dsjVal: '',
        xqjOptions: [],
        xqjVal: '',
        gdsOptions: [],
        gdsVal: ''
      },
      yhCodeInput: '',
      //表格
      pages: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      loading: false,
      //弹窗组件
      dialogTitle: '',
      dialogVisible: false,
      componentName: '',

    }
  },
  mounted() {
    this.GetPowerAreaCode()//地区
    this.GetTdmgCustList()
  },
  methods: {
    handleSizeChange(val) {
      this.pages.pageSize = val
      this.GetTdmgCustList()
      window.console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pages.pageNo = val
      this.GetTdmgCustList()
      window.console.log(`当前页: ${val}`);
    },
    handleClose(done) {
      done();
      // this.$confirm('确认关闭？')
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    changeComponents(v) {
      if (v === 1) {
        this.dialogTitle = '停电敏感算法识别客户清单明细'
        this.componentName = 'outPowerListDetailed'
        this.dialogVisible = true;
      } else if (v === 2) {
        this.dialogTitle = '客户群体特征分析'
        this.componentName = 'distributionCharts'
        this.dialogVisible = true;
      } else if (v === 3) {
        this.dialogTitle = '手工维护客户清单明细'
        this.componentName = 'manualListDetails'
        this.dialogVisible = true;
      } else if (v === 4) {
        this.dialogTitle = '停电敏感客户手工维护'
        this.componentName = 'manualManage'
        this.dialogVisible = true;
      }
    },
    //获取供电单位编码--地市局
    GetPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.selectParams.dsjOptions = res
      })
    },
    //获取区县局--区县局
    GetQxjPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.selectParams.dsjVal //上一级编码
      }).then(res => {
        this.selectParams.xqjOptions = res
      })
    },
    //获取供电所
    GetGdsPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.selectParams.xqjVal //上一级编码
      }).then(res => {
        this.selectParams.gdsOptions = res
      })
    },
    //地市局选择
    dsjChange() {
      this.selectParams.gdsVal = ''
      this.selectParams.xqjVal = ''
      this.selectParams.gdsOptions = []
      this.selectParams.xqjOptions = []
      this.GetQxjPowerAreaCode()
    },
    //区县局选择
    qxjChange() {
      this.selectParams.gdjVal = ''
      this.selectParams.gdsOptions = []
      this.GetGdsPowerAreaCode()
    },

    //停电敏感客户明细列表
    GetTdmgCustList() {
      this.getTdmgCustList({
        "dsj": this.selectParams.dsjVal,
        "xqj": this.selectParams.xqjVal,
        "gds": this.selectParams.gdsVal,
        "yhbh": this.yhCodeInput,
        "size": this.pages.pageSize,
        "current": this.pages.pageNo
      }).then(res => {
        this.pages.total = res.total
        this.tableData = res.records
      })
    }
  }
}
</script>

<style scoped lang="scss">
.featuresUser {
  .input_td {
    width: 200px;
    margin-right: 20px;
  }
  .input_300 {
    width: 300px;
    margin-right: 20px;
  }
  .selectBut {
    .el-button {
      margin-right: 12px;
    }
  }
  .header {
    line-height: 30px;
    .selectNav {
      height: 30px;
      margin-bottom: 16px;
    }
    .selectBut {
      height: 30px;
      margin-bottom: 16px;
    }
  }
  .content {
    .title {
      padding-left: 18px;
      border-left: 6px solid #1a84f4;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .tableTd {
      .block {
        text-align: center;
        margin-top: 10px;
      }
    }
  }
}
</style>